import React from "react";

import "./sign-in.style.scss";

import FormInput from "../form-input/form-input.component"
import CustomButton from "../custom-button/custom-button.component"

import {auth, signInWithGoogle} from "../../firebase/firebase.utils"

class SignIn extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
    };
  }
    handleSubmit = async event => {
        event.preventDefault()
        const { email, password } = this.state
      try {
        await auth.signInWithEmailAndPassword(email, password)
        this.setState({ email: "", password:""})
      } catch (error) {
        console.log(error)
       }
      
    }
    handleChange = event => {
        const { value, name } = event.target
        this.setState({[name]:value})
    }
  render() {
    return (
      <div className="sign-in">
        <h2> I already have an account</h2>
        <span> Sign in with your email and your password</span>

        <form onSubmit={this.handleSubmit}>
          <FormInput name="email" handleChange={this.handleChange} type="email" label="Email" value={this.state.email} />

          <FormInput name="password" handleChange={this.handleChange} type="password" label="Password" value={this.state.password} />
          <div className="buttons">
            <CustomButton type="submit"> Sign In </CustomButton>
            <CustomButton onClick={signInWithGoogle} isGoogleSignIn > Sign In  with Google</CustomButton>
          </div>
        </form>
      </div>
    );
  }
}
export default SignIn 